.time {
  text-align: center;
  padding: 32px;
  background-blend-mode: multiply;
  position: relative;
}

.time h3 {
  font-size: 32px;
  font-family: "Prata";
  border-bottom: 4px solid;
  display: inline-block;
  padding-bottom: 8px;
}

.time .colaboradores {
  display: flex;
  justify-content: space-around;
  margin-top: 32px;
  flex-wrap: wrap;
}

.input-color {
  position: absolute;
  top: 20px;
  right: 50px;
}

.input-colorP {
  position: absolute;
  top: 20px;
  left: 50px;
}
